import * as React from 'react';

const ArticleItem = function ({tagName}: {tagName?: string}) {
  return (
    <div>
      <h3><a href="/jstt/bj/2017-07-13/784.html"></a></h3>
      <figure><img src={require('../../images/img_1.jpg')} alt="" /></figure>
      <ul>
        <p> </p>
        <a title="" href="/jstt/bj/2017-07-13/784.html" target="_blank" className="readmore">&gt;&gt;</a>
      </ul>
      <p className="dateview"><span>2017-07-13</span><span></span><span>[<a href="/jstt/bj/"></a>]</span></p>
    </div>
  );
};

const BlogList = function () {
  return (
    <div className="bloglist f_l" >
      {[1, 2, 3].map((item, index) => {
        return <ArticleItem key={index} />;
      })}
    </div>
  );
};

export { BlogList };
export default ArticleItem;


